<template>
  <div>
    <h1>okkkkkkkkkkkkk</h1>
  </div>
</template>

<script>
import Layout from "../../../layouts/main";
import appConfig from "@/app.config";
import PageHeader from "@/components/page-header";
import simplebar from "simplebar-vue";
import StatisticService from "@/helpers/statistik.service";
import UserService from "@/helpers/user.service";
import store from '@/state/store';
import moment from 'moment';

import {
  overviewChart,
  transactionsData,
  bitconinChart,
  ethereumChart,
  litecoinChart
} from "./data";

export default {
  page: {
    title: "Secure ME Dashboard",
    meta: [{ name: "description", content: appConfig.description }]
  },
  components: { Layout, PageHeader, simplebar },
  data() {

    let finishedDailyChart = {
        series: [{
            name: 'Report',
            data: []
        }],
    };
    return {
      user_type: store.state.authBackend.user.type,
      overviewChart: overviewChart,
      transactionsData: transactionsData,
      bitconinChart: bitconinChart,
      ethereumChart: ethereumChart,
      litecoinChart: litecoinChart,
      finishedDailyChart,
      caseReportChart:{
         series: []
      },
      items: [
        {
          text: "Dashboards",
          href: "/"
        },
      ],
      x_cat:[],
      theme:"",
      x_category:[],
      x_color:[],
      title: "Secure ME",
      total_agent: '',
      total_customer: '',
      total_subscribe: '',
      total_admin: '',
      total_cs: '',
      total_package: '',
      total_region: '',
      total_upload: '',
      total_case: '',
      total_report: '',
      top_agent:[],
      top_agent_list:'',
      recent_report:'',
      agent_last_update:'',
      loading_agent_last: false,
      loading_top_agent: false,
      loading_recent_report: false
    };
  },
  methods: {
    list(){
      UserService.getProfile()
      .then(response => {
        // console.log(response.data.data);
        this.user_type = response.data.data.admin.role;
      })
      .catch(e => {
        this.message = e.response.data.data
      });
    },
    totalCase(){
      StatisticService.getTotalCase()
      .then(response => {
        // console.log(response.data.data);
        this.total_case = response.data.data;
      })
      .catch(e => {
        this.message = e.response.data.data
      });
    },
    totalAdmin(){
      StatisticService.getTotalAdmin()
      .then(response => {
        // console.log(response.data.data);
        this.total_admin = response.data.data;
      })
      .catch(e => {
        this.message = e.response.data.data
      });
    },
    totalCs(){
      StatisticService.getTotalCs()
      .then(response => {
        // console.log(response.data.data);
        this.total_cs = response.data.data;
      })
      .catch(e => {
        this.message = e.response.data.data
      });
    },
    totalAgent(){
      StatisticService.getTotalAgent()
      .then(response => {
        // console.log(response.data.data);
        this.total_agent = response.data.data;
      })
      .catch(e => {
        this.message = e.response.data.data;
      });
    },
    totalSubcribe(){
      console.log('mulai');
      StatisticService.getTotalSubcribe()
      .then(response => {
        console.log(response.data.data);
        this.total_subscribe = response.data.data;
      })
      .catch(e => {
        this.message = e.response.data.data;
      });
    },
    totalCustomer(){
      StatisticService.getTotalCustomer()
      .then(response => {
        // console.log(response.data.data);
        this.total_customer = response.data.data;
      })
      .catch(e => {
        this.message = e.response.data.data
      });
    },
    totalPackage(){
      StatisticService.getTotalPackage()
      .then(response => {
        // console.log(response.data.data);
        this.total_package = response.data.data;
      })
      .catch(e => {
        this.message = e.response.data.data
      });
    },
    totalRegion(){
      StatisticService.getTotalRegion()
      .then(response => {
        // console.log(response.data.data);
        this.total_region = response.data.data;
      })
      .catch(e => {
        this.message = e.response.data.data
      });
    },
    totalUpload(){
      StatisticService.gettotalUpload()
      .then(response => {
        // console.log(response.data.data);
        this.total_upload = response.data.data;
      })
      .catch(e => {
        this.message = e.response.data.data
      });
    },
    topAgent(){
      this.loading_top_agent = true;
      StatisticService.getTopAgent()
      .then(response => {
        // console.log(response.data.data);
        this.top_agent = response.data.data[0];
        this.top_agent_list = response.data.data;
        this.loading_top_agent = false;
      })
      .catch(e => {
        this.message = e.response.data.data;
        this.loading_top_agent = false;
      });
    },
    totalReport(){
      StatisticService.getTotalReport()
      .then(response => {
        // console.log(response.data.data);
        this.total_report = response.data.data;
      })
      .catch(e => {
        this.message = e.response.data.data
      });
    },
    finishedDaily(){
      StatisticService.getFinishedDaily()
      .then(response => {
        // console.log(response.data.data);
          let asc = response.data.data;
          let sortedArray  = asc.sort((a,b) => new moment(a.date).format('YYYYMMDDhhmmss') - new moment(b.date).format('YYYYMMDDhhmmss'));
          let datasorting = sortedArray;

          datasorting.forEach(event => {
          this.x_cat.push(event.label)
          this.finishedDailyChart.series[0].data.push(event.finished_daily_report)
        })
      })
      .catch(e => {
        this.message = e.response.data.data
      });
    },
    CategoryReport(){
      this.caseReportChart.series = [];
      this.x_color = [];
      StatisticService.getCategoryReport()
      .then(response => {
          let cat = response.data.data;
          cat.forEach(event => {
            // console.log(event.category, event.total);
            this.caseReportChart.series.push(event.total);
            this.x_category.push(event.category);
            if(event.category == 'low'){
              this.x_color.push('#34C38F');
            } else if(event.category == 'medium'){
              this.x_color.push('#50A5F1');
            } else if(event.category == 'high'){
              this.x_color.push('#CF5A5A');
            }
            // ['#34C38F', '#50A5F1', '#000']
        })
      })
      .catch(e => {
        this.message = e.response.data.data
      });
    },
    RecentReport(){
      this.loading_recent_report = true;
      StatisticService.getRecentReport()
      .then(response => {
          //  console.log(response.data.data);
           this.recent_report = response.data.data;
           this.loading_recent_report = false;
      })
      .catch(e => {
        this.message = e.response.data.data;
        this.loading_recent_report = false;
      });
    },
    AgentLastReport(){
      this.loading_agent_last = true;
      StatisticService.getAgentLastUpdate()
      .then(response => {
          //  console.log(response.data.data);
          this.agent_last_update = response.data.data;
          this.loading_agent_last = false;
      })
      .catch(e => {
        this.message = e.response.data.data;
        this.loading_agent_last = false;
      });
    },
    formatDate(value){
      return moment(value).format("YYYY-MM-DD, hh:mm");
    }
  },
  mounted(){
    this.theme = localStorage.getItem('theme');
    this.list();
    this.topAgent();
    this.totalCase();
    this.totalCs();
    this.totalAdmin();
    this.totalSubcribe();
    this.totalAgent();
    this.totalCustomer();
    this.totalPackage();
    this.totalRegion();
    this.totalUpload();
    this.totalReport();
    this.finishedDaily();
    this.RecentReport();
    this.AgentLastReport();
    this.CategoryReport();
  }
};
</script>

<style scope>
.text-muted{
  font-size:15px;
  font-weight: 500;
}
</style>
